import React, { memo, useEffect, useState, useRef } from 'react';
import { useDispatch, useSelector, shallowEqual } from 'react-redux';

import {
  getBanner
} from '../../store/actionCreators';

import { Carousel } from 'antd';
import {
  BannerWrapper,
  BannerLeft,
  BannerRight,
  BannerControl
} from './style';

export default memo(function HYTopBanner() {
  //currentIndex状态变量 setCurrentIndex是函数改变currentIndex
  const [currentIndex, setCurrentIndex] = useState(0);

  const dispatch = useDispatch();
  const state = useSelector(state => ({
    banners: state.getIn(["recommend", "topBanners"])
  }), shallowEqual)

  const bannerRef = useRef();

  //[dispatch]检测dispatch ，dispatch发生改变 调佣() => {dispatch(getBanner());
  useEffect(() => {
    dispatch(getBanner());
  }, [dispatch]);

  //from 随着 Carousel改变
  
  const bannerChange = (from,to) => {
    setCurrentIndex(to);
  }

  //变色背景 state.banners[currentIndex] &&保证 state.banners[currentIndex]不为undeinfed
  //"?imageView&blur=40x20"图片模糊了 
  const bgImage = state.banners[currentIndex] && (state.banners[currentIndex].imageUrl + "?imageView&blur=40x20")
  

  return (
    <BannerWrapper bgImage={bgImage}>
      <div className="banner wrap-v2">
        <BannerLeft>
          <Carousel autoplay effect="fade" beforeChange={bannerChange}  ref={bannerRef}>
            {
              state.banners.map((item, index) => {
                return (
                  <div className="banner-item" key={item.imageUrl}>
                    <img className="image" src={item.imageUrl} alt={item.typeTitle} />
                  </div>
                )
              })
            }
          </Carousel>
        </BannerLeft>
        <BannerRight>
        </BannerRight>
        <BannerControl className="control">
          <button className="btn left" onClick={e => bannerRef.current.prev()}></button>
          <button className="btn right" onClick={e => bannerRef.current.next()}></button>
        </BannerControl>
      </div>
    </BannerWrapper>
  )
})
