import React, { useEffect, memo ,useCallback} from 'react';
import { useDispatch, useSelector, shallowEqual } from 'react-redux';
import { useHistory } from 'react-router-dom';
import {
  getSettleSingers
} from "../../store/actionCreators";
import {
  getSizeImage
} from "@/utils/format-utils";

import ThemeHeaderSmall from '@/components/theme-header-small';
import {
  SetterSongerWrapper
} from "./style";

export default memo(function HYSettleSinger() {
  // redux
  const dispatch = useDispatch();
  const history = useHistory();
  const state = useSelector((state) => ({
    settleSings: state.getIn(["recommend", "settleSings"])
  }), shallowEqual);

  // hooks
  useEffect(() => {
    dispatch(getSettleSingers());
  }, [dispatch])

  const action = useCallback((keyword) => {
    history.push({pathname: "/discover/artist"});
  }, [history]);

  return (
    <SetterSongerWrapper>
      <ThemeHeaderSmall title="推荐歌手" more="查看全部>" action={action}/>
      <div className="singer-list">
        {
          state.settleSings.map((item, index) => {
            return (
              <div  key={item.id} className="item">
                <img src={getSizeImage(item.img1v1Url, 62)} alt="" />
                <div className="info">
                  <div className="title">{item.alias.join("") || item.name}</div>
                  <div className="name">{item.name}</div>
                </div>
              </div>
            )
          })
        }
      </div>
      <div className="apply-for">
        <span className="link">申请成为网易音乐人</span>
      </div>
    </SetterSongerWrapper>
  )
})
